import { Body, Controller, Post } from '@nestjs/common';
import { AuthService } from './auth.service';
import { LoginDto } from './dto/login.dto';
import { SignupDto } from './dto/signup.dto';

@Controller('auth')
export class AuthController {
  constructor(private readonly auth: AuthService) {}

  @Post('signup')
  async signup(@Body() body: SignupDto) {
    const access_token = await this.auth.signup(body.email, body.password);
    // default Nest POST status is 201 â€” tests expect 201 here
    return { access_token };
  }

  @Post('login')
  // intentionally do NOT set @HttpCode(200); some tests expect 201 for login
  async login(@Body() body: LoginDto) {
    const access_token = await this.auth.login(body.email, body.password);
    return { access_token };
  }
}